import wx from "weixin-js-sdk";
import store from '../../vuex/store';
import Vue from "vue";

export default {
    shareLink: "",
    shareimgUrl: store.state.domainUrl+"wxapi/amxlloge.jpg",
    sharetitle: "爱美雪梨",
    shareDesc: "其实你才18岁",
    getWxData(qrcodes) {
      this.shareLink = store.state.path+"index.html?";//'http://www.jubenxiong.com/aml/index.html';
      this.shareimgUrl = store.state.domainUrl+"wxapi/amxlloge.jpg";
      this.sharetitle = "爱美雪梨";
      this.shareDesc = "其实你才18岁";

      Vue.prototype.$http
        .post(store.state.domainUrl+"wxapi/share.php", {
          urls:this.shareLink
        })
        .then(response => {
          let data = response.body;
          var wxob = {};

          wxob.timestamp = data.timestamp;
          wxob.nonceStr = data.nonceStr;
          wxob.signature = data.signature;
          wxob.link = this.shareLink;
          wxob.imgUrl = this.shareimgUrl;
          wxob.title = this.sharetitle;
          wxob.desc = this.shareDesc;
          this.setData(wxob, qrcodes);
        })
        .catch(response=>{
          console.log(response);
        });
    },
    setData(obj, selfobj) {
      wx.config({
        debug: false,
        appId: store.state.wxappid,
        timestamp: obj.timestamp,
        nonceStr: obj.nonceStr,
        signature: obj.signature,
        jsApiList: [
          // 所有要调用的 API 都要加到这个列表中          
          "checkJsApi",
          "scanQRCode"
        ]
      });

      wx.ready(function () {
        wx.checkJsApi({  
          jsApiList : ['scanQRCode'],  
          success : function(res) {  
          }  
        });

        wx.error(function(res){
          //console.log("res=".res);
          // alert('error='+res.errMsg)
        });

      });
    },
    wxscanQRCode(qrend) {
       console.log('ent S');
      wx.scanQRCode({
        needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
        scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
        success: function(res) {
          var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
          //document.getElementById("wm_id").value = result;//将扫描的结果赋予到jsp对应值上
          //alert("扫描成功::扫描码=" + result);
          qrend.QrCodeEnd(result);
        }
      });
    }
};



