<template>
    <div class="pasenges">
      <div class="top-bg"></div>
      <div class="div-placeholder"></div>
      <div class="frame header-section">
          <div class="identity">
            <div class="identity-left">
              <img @click="headClickHandle" class="head-img" :src="icon"/>
              <span v-show="!token" @click="headClickHandle" class="name login-word">立即登录</span>
              <span v-show="token" class="name">{{username}}</span>
            </div>
            <div class="identity-right"><span class="job">{{roleName}}</span></div>
          </div>
          <div class="data">
            <div class="data-item">
                <p class="num">{{accounts}}</p>
                <p class="data-name">余额</p>
            </div>
            <div class="data-item">
                <p class="num">{{source}}</p>
                <p class="data-name">积分</p>
            </div>
            <div class="data-item">
                <p class="num">{{expnum}}</p>
                <p class="data-name">体验券</p>
            </div>
            <div class="data-item">
                <p class="num">{{giftnum}}</p>
                <p class="data-name">礼品券</p>
            </div>
          </div>
      </div>
      <div class="frame service-section">
        <p class="frame-title">我的服务</p>
        <div class="function-cell-list">
          <div v-for="item in serviceArr" class="function-cell" @click="cellClickHandle(item,$event)">
            <span :class="'iconfont '+item.icon"></span>
            <p class="icon-name">{{item.names}}</p>
          </div>
        </div>
      </div>
      <div v-show="this.helpArr&&this.helpArr.length!=0" class="frame help-section">
        <p class="frame-title">帮助中心</p>
        <div class="function-cell-list">
          <div v-for="item in helpArr" class="function-cell" @click="cellClickHandle(item,$event)">
            <span :class="'iconfont '+item.icon"></span>
            <p class="icon-name">{{item.names}}</p>
          </div>
        </div>
      </div>
      <mt-button v-show="token" size="large" class="login-out-btn" type="default" @click.native="loginOutHandle">退出登录</mt-button>
      <div class="change-name-popup" v-show="showChangeNamePop">
        <div class="pop-content">
          <p class="change-name-tip">*请修改您的商家名称*</p>
          <mt-button size="small" class="change-btn" type="default" @click="jumpToChangeName">去改名</mt-button>
        </div>
        <div class="pop-bg"></div>
      </div>
    </div>
</template>
<script>
import meConfig from "./meConfig";
import wxpay from "../../common//js/wxpay";
import { mapState } from "vuex";
import Vue from "vue";
import wxqrcode from "../../common/js/wxqrcode";
import { Toast, Button } from "mint-ui";
import "mint-ui/lib/button/style.css";
import "mint-ui/lib/toast/style.css";
Vue.component(Button.name, Button);
Vue.component(Toast.name, Toast);

export default {
  name: "v-me",
  data() {
    return {
      roleName: "",
      serviceArr: [],
      helpArr: null,
      resultObj: {},
      intervalId: -1000
    };
  },
  components: {},
  mounted() {
    this.wxReady();
    this.initFunction();
  },
  beforeRouteLeave(to, from, next) {
    // this.$store.commit("UPDATE_NOBINDSHOW", false);
    if (this.intervalId != -1000) {
      window.clearInterval(this.intervalId);
      this.intervalId = -1000;
    }
    next();
  },
  watch: {
    token: "initFunction",
    role: "initFunction"
  },
  activated() {
    document.body.scrollTop = 0;
    this.refreshInfoInTime();
    // this.initFunction();
    if (this.intervalId == -1000)
      this.intervalId = setInterval(this.intervalHandle, 3000);
    this.$store.dispatch("toggleLoading", false);
  },
  methods: {
    jumpToChangeName() {
      this.$router.push({ name: "bindname" });
    },
    intervalHandle() {
      if (!this.token) return;
      this.refreshInfoInTime();
    },
    initFunction() {
      console.log("this.role=" + this.role);
      let configItem = meConfig[this.role.toString()];
      if (this.token) this.roleName = configItem.roleName;
      else this.roleName = "访客";
      this.serviceArr = configItem.service;
      this.helpArr = configItem.help;
    },
    wxReady() {
      wxqrcode.getWxData(this);
    },
    QrCodeEnd(results) {
      //results
      // results = "fuc:bind,userid:20";
      // results = "fuc:integral,userid:136,id:5aa4e3e2ab7c1";
      var resultsArr = results.split(",");
      if (resultsArr.length < 2) {
        return;
      }
      resultsArr.forEach((val, index, arr) => {
        let tempArr = val.split(":");
        this.resultObj[tempArr[0]] = tempArr[1];
      });

      // alert('results='+results);

      if (!this.resultObj.fuc) return;
      if (this.resultObj.fuc == "good") {
        //礼品券扫码
        this.requestUseGood();
      } else if (this.resultObj.fuc == "bind") {
        //绑定关系扫码
        this.requestCanBind();
        console.log("this.resultObj.fuc=bind");
      } else if (this.resultObj.fuc == "integral") {
        this.requestUseIntegral();
      }
    },
    requestCanBind() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          fuc: "deposit_canpay",
          clientid: this.userid,
          salesmanid: this.resultObj.userid,
          dptype: 1
        })
        .then(response => {
          let data = response.body;
          let irv = data.toString();
          if (irv == "200") {
            this.jumpToPledgePage();
          } else if (irv == "105") {
            Toast({
              message: "您不是普通客户!",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "106") {
            Toast({
              message: "对方不是业务员!",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "107") {
            Toast({
              message: "双方已绑定过了!",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "108") {
            Toast({
              message: "您已经绑定了其他商户!",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else {
            Toast({
              message: "绑定失败！",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    jumpToPledgePage() {
      var cookieStr =
        "salesmanid:" +
        this.resultObj.userid +
        ",types:DepositPayParam,userid:" +
        this.userid;
      wxpay.GetPayCode(cookieStr);
    },
    requestUseGood() {
      this.$store.dispatch("toggleLoading", true);
      // alert('resultObj='+this.resultObj.id+'=ddd='+this.resultObj.userid+'bid='+this.userid);

      this.$http
        .post(this.path + `php/suncode/dealfuc.php`, {
          fuc: "usecoupon",
          cuid: this.resultObj.id,
          userid: this.resultObj.userid,
          bossuserid: this.userid,
          buyid: this.resultObj.buyId
        })
        .then(response => {
          let data = response.body;
          if (data.toString() == "200") {
            this.scanGoodSuccessHandle();
          } else if (data.toString() == "202") {
            Toast({
              message: "你不是商户",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (data.toString() == "201") {
            Toast({
              message: "礼品券不存在",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (data.toString() == "203") {
            Toast({
              message: "角色不存在",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (data.toString() == "205") {
            Toast({
              message: "该客户已经和其他商户绑定了关系",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (data.toString() == "206") {
            Toast({
              message: "不在使用时间范围内",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (data.toString() == "303") {
            Toast({
              message: "代理商不存在",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    requestUseIntegral() {
      this.$store.dispatch("toggleLoading", true);
      this.$http
        .post(this.path + `php/Route.php`, {
          type: "usesourcegoods",
          uid: this.resultObj.userid,
          bid: this.userid,
          oid: this.resultObj.id
        })
        .then(response => {
          let irv = response.body.error.toString();
          if (irv == "ok") {
            this.scanGoodSuccessHandle();
          } else if (irv == "-100") {
            Toast({
              message: "用户不存在",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-101") {
            Toast({
              message: "商户不存在",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-102") {
            Toast({
              message: "用户身份不对",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-103") {
            Toast({
              message: "商户身份不对",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-104") {
            Toast({
              message: "修改失败",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-105") {
            Toast({
              message: "订单没找到",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-106") {
            Toast({
              message: "订单信息错误",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          } else if (irv == "-107") {
            Toast({
              message: "订单已完结",
              iconClass: "iconfont icon-tishi1",
              duration: 3000
            });
          }
          this.$store.dispatch("toggleLoading", false);
        })
        .catch(response => {
          //请求出错处理
          console.log(response);
        });
    },
    scanGoodSuccessHandle() {
      Toast({
        message: "操作成功",
        iconClass: "iconfont icon-msnui-success",
        duration: 3000
      });
      //   this.$router.go(-1);
      this.updatateUserInfo(this.initFunction);
    },
    cellClickHandle(cellData, event) {
      ////this.onScanQr(event);
      //this.QrCodeEnd("fuc:integral,userid:21,id:5aa3e888ab21c");
      //this.QrCodeEnd("");//
      if (cellData.names == "扫一扫") this.onScanQr(event);
      else if (cellData.names == "联系我们")
        window.location.href = "tel:4001368868";
      else this.$router.push({ name: cellData.tourl });
    },
    onScanQr(event) {
      if (!this.token) {
        this.loginHandle();
        return;
      }

      // if (event._constructed) return;
      //   this.QrCodeEnd("fuc:good,userid:1,id:2");
      wxqrcode.wxscanQRCode(this);
      // this.QrCodeEnd("fuc:bind,userid:11");
    },
    loginOutHandle() {
      if (this.token) {
        this.resetUserData();
      }
      this.loginHandle();
    },
    loginHandle() {
      this.$router.push({ path: "/login" });
    },
    headClickHandle() {
      if (this.token) return;
      this.loginHandle();
    }
  },
  computed: {
    ...mapState([
      "openid",
      "path",
      "userid",
      "accounts",
      "role",
      "clientNum",
      "giftnum",
      "expnum",
      "username",
      "icon",
      "phoneno",
      "source",
      "token"
    ]),
    showChangeNamePop(){
      //if(this.username&&this.username.toString()==this.phoneno.toString()) return true;
      if(this.role.toString()=="2"&&this.username&&this.username.toString()==this.phoneno.toString()) return true;
      else return false;
    }
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
.pasenges {
  width: 100%;
  box-sizing: border-box;
  min-height: 100%;
  padding-bottom: 0.48rem;
  position: relative;

  .change-name-popup {
    .pop-content {
      position: fixed;
      transform: translate(-50%, -50%);
      left: 50%;
      top: 50%;
      z-index: 19;
      width: 80%;
      border-radius: 8px;
      background-color: #fff;
      text-align: center;
      padding-top: 0.34rem;
      padding-bottom: 0.25rem;

      .change-name-tip {
        color: #595656;
        font-size: 0.16rem;
        letter-spacing: 0.01rem;
      }

      .change-btn {
        margin-top: 0.25rem;
        width: 60%;
        height: 0.3rem;
        background-color: #f8ef94;
        color: #595656;
        letter-spacing: 0.02rem;
        font-size: 0.15rem;
      }
    }

    .pop-bg {
      position: fixed;
      z-index: 18;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background-color: #000;
      opacity: 0.4;
    }
  }

  .top-bg {
    position: absolute;
    top: 0;
    width: 100%;
    height: 1.1rem;
    background-color: #f8ef94;
  }

  .div-placeholder {
    height: 0.32rem;
  }

  .frame {
    margin: 0 0.1rem;
    background-color: #fff;
    border-radius: 0.06rem;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;

    .frame-title {
      font-size: 0.12rem;
      color: #595656;
      margin: 0.12rem 0 0.18rem 0.24rem;
    }

    .function-cell-list {
      display: flex;
      flex-wrap: wrap;
      margin: 0 0.13rem;

      .function-cell {
        margin-bottom: 0.26rem;
        text-align: center;
        flex: 0 0 25%;

        .iconfont {
          font-size: 0.3rem;
          color: #eae135;
        }

        .icon-name {
          margin-top: 0.08rem;
          font-size: 0.12rem;
          color: #595656;
        }
      }
    }
  }

  .header-section {
    position: relative;
    z-index: 1;
    height: 1.5rem;
    padding: 0 0.24rem;

    .identity {
      flex: 0 1 0.8rem;
      display: flex;
      justify-content: space-between;
      align-items: center;

      .identity-left {
        display: flex;
        align-items: center;

        .head-img {
          width: 0.5rem;
          height: 0.5rem;
          border-radius: 50%;
        }

        .name {
          margin-left: 0.14rem;
          font-size: 0.12rem;
          color: #595656;

          &.login-word {
            letter-spacing: 0.01rem;
          }
        }
      }

      .identity-right {
        .job {
          display: inline-block;
          color: #595656;
          font-size: 0.12rem;
          background-color: #f8ef94;
          width: 0.62rem;
          text-align: center;
          border-radius: 0.2rem;
          letter-spacing: 0.01rem;
          padding-top: 0.05rem;
          padding-bottom: 0.03rem;
        }
      }
    }

    .data {
      display: flex;
      justify-content: space-between;

      .data-item {
        text-align: center;

        .num {
          margin-top: 0.03rem;
          font-size: 0.14rem;
          color: #00a0e9;
        }

        .data-name {
          font-size: 0.12rem;
          color: #595656;
          letter-spacing: 0.01rem;
          margin-top: 0.12rem;
        }
      }
    }
  }

  .service-section {
    margin-top: 0.1rem;
    min-height: 1.76rem;
  }

  .help-section {
    margin-top: 0.1rem;
  }

  .login-out-btn {
    background-color: #f8ef94;
    color: #595656;
    margin-top: 0.1rem;
    margin-left: 0.25rem;
    width: calc(100% - 0.5rem);
    font-size: 0.12rem;
    letter-spacing: 0.01rem;
    height: 0.32rem;
    box-shadow: none;
  }
}
</style>



